aside.eltdf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {
        color: $default-text-color;

        .product_list_widget{
            .star-rating{
                display: none;
            }

            .product-title{
                margin-top: 15px;
                font-weight: 400!important;
                letter-spacing: 0px;
            }

            del, ins{
                margin-top: 3px !important;

            }

            del {
                margin-right: 10px !important;
            }

            del, li > .woocommerce-Price-amount.amount{
                color: #c3c3c3;
            }

            ins, li > .woocommerce-Price-amount.amount{
                color: $first-main-color;
            }
        }

        &:last-child {
            margin: 0 !important;
        }
    }
}

.wpb_widgetised_column {
    position: relative;

}

.eltdf-side-menu .eltdf-cli-text *{
    font-weight: 300;
}

aside.eltdf-sidebar,
.wpb_widgetised_column {
    // widgets spacing
    $widget-bottom-margin: 30px;
    $widget-bottom-padding: 27px;
    $widget-inner-padding: 27px;

    // widget color
    $widget-background-color: #f9f9f9;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 6px;

    // list items color
    $widget-list-item-background-color: #ffffff;

    // list items border
    $widget-list-item-border-color: $default-border-color;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: solid;

    // text
    $widget-text-color: inherit;
    $widget-heading-color: inherit;

    // links
    $widget-link-color: $default-heading-color;
    $widget-link-hover-color: darken($default-text-color, 35%);

    // specific widgets
    $widget-search-form-height: 48px;
    $widget-tag-cloud-height: 30px;

    @import 'widgets/default-wordpress-widgets-boxed';
    @import 'widgets/standalone-widgets';
}

.widget {

    .eltdf-widget-title-holder .eltdf-widget-title {
        margin: 0 0 9px;
    }

    &.eltdf-course-list-widget, &.eltdf-blog-list-widget {
        .eltdf-widget-title-holder .eltdf-widget-title {
            margin: 4px 0 23px;
        }
    }

    &.widget_products{
        .eltdf-widget-title-holder .eltdf-widget-title {
            margin: 0 0 16px;
        }
    }

}