/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */
.widget {
    background-color: $widget-background-color;
    margin: 0 0 $widget-bottom-margin;
    padding: 31px 26px 40px 26px;
    box-sizing: border-box;

    .eltdf-widget-title-holder {

        .eltdf-widget-title {
            color: $default-heading-color;
            margin-top: 0;
            font-weight: 700;

        }

        .eltdf-separator {
            border-bottom-width: 3px;
            border-color: $widget-text-color;
            width: 30px;
        }
    }

    ul {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            display: block;
            line-height: $widget-list-item-height;
            margin: 0;
            padding: $widget-list-item-padding 0;
            font-family: $default-heading-font;
            font-weight: 400;

            &:last-child {
                border: 0;
                padding-bottom: 0;
            }

            a {
                color: $widget-link-color;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }
        }

        .children,
        .sub-menu {
            border-top: 1px solid $widget-list-item-border-color;
            margin-top: $widget-list-item-padding;
            padding-left: 10px;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and animation on link hover


                a {

                    @include eltdfTransition(padding .2s ease);

                    &:hover {
                        text-decoration: underline;
                    }
                }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and w/o animation on link hover

    &.widget_recent_comments,
    &.widget_rss {

        .eltdf-widget-title {
            margin-bottom: 0;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // calendar

    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            background-color: $widget-list-item-background-color;
            color: $widget-text-color;
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // rss

    &.widget_rss {
        .rss-widget-icon {
            display: inline-block;
        }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // search

    .eltdf-searchform {
        .input-holder {
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $widget-text-color;
                display: inline-block;
                float: left;
                font-family: $default-text-font;
                font-size: 15px;
                font-weight: 300;
                height: $widget-search-form-height;
                line-height: calc(#{$widget-search-form-height} - 2px);
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;

                @include eltdfTransition(color .2s ease-in-out);
            }

            input.search-field {
                padding: 0 0 0 21px;
                width: calc(100% - #{$widget-search-form-height});

                @include eltdfPlaceholder {
                    color: $widget-text-color;
                }
            }

            button {
                background: $widget-list-item-background-color;
                color: $widget-text-color;
                cursor: pointer;
                font-size: 15px;
                width: $widget-search-form-height;

                @include eltdfTransition(color .2s ease-in-out);

                &:hover {
                    color: $widget-link-hover-color;
                }

                span {
                    display: block;
                    left: 0;
                    line-height: inherit;
                    position: absolute;
                    top: 0;
                    width: 100%;
                    color: #777;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // combobox

    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-radius: 0;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: 500;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-list-item-padding;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;

            @include eltdfTransition(color .2s ease-in-out);

            option {
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // tag cloud

    .tagcloud {
        a {
            border: 1px solid $default-heading-color;
            background-color: $default-heading-color;
            color: #fff;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 11px !important;
            font-weight: 500; // medium
            line-height: $widget-tag-cloud-height;
            margin: 0 7px 7px 0;
            padding: 0 19px;
            text-transform: uppercase;

            @include eltdfTransition(all .2s ease-in-out);

            &:hover {
                color: #fff;
                border-color: $first-main-color;
                background-color: $first-main-color;
                text-decoration: none;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // text

    &.widget_text {
        img {
            display: block;
        }

        p {
            &:first-child {
                margin-top: 0;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}