@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

p.comment-form-cookies-consent{
    margin: 5px 0 25px 0;
}

.eltdf-comment-holder {
    @include eltdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .eltdf-comment-holder-inner {
        padding: 72px 0 20px;
    }

    .eltdf-comment {

        &.eltdf-pingback-comment {

            .eltdf-comment-text {
                padding: 0;
            }
        }
    }

    .eltdf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include eltdfRelativeHolderLayout();
            margin: 25px 0 0;
            padding: 22px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 94px;
        }
    }

    .eltdf-comments-title {
        margin-bottom: 30px;

        > * {
            margin: 0;
        }
    }

    .eltdf-comment-image {
        @include eltdfTableCellLayout();
        vertical-align: top;
        padding-top: 12px;
        width: 112px;
        text-align: center;
        line-height: 0;

        img {
            display: inline-block;
            border-radius: 50%;
            width: 100%;

        }
    }

    .eltdf-comment-text {
        @include eltdfTableCellLayout();
        width: calc(100% - 112px);
        padding-left: 27px;

        .eltdf-comment-info {
            @include eltdfRelativeHolderLayout();
            margin: 6px 0 0;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            font-size: 14px;
            font-family: $default-heading-font;
            font-weight: 500;
            color: $default-heading-color;
            display: inline-block;
            margin-right: 5px;
            text-transform: capitalize;

            &:hover {
                color: $first-main-color;
            }

            &:after{
                 content: '';
                 display: block;
                 border-bottom: 1px solid;
                 border-color: inherit;
                 position: relative;
                 top: -4px;
            }
        }

        .eltdf-comment-name {
            float: left;
            margin: 0;
        }

        .eltdf-comment-date {
            float: left;
            margin: 0;
            font-size: 13px;
            font-family: $default-heading-font;
            font-weight: 400;
            margin-left: 18px;
        }

        .eltdf-text-holder p {
            margin: 4px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            font-size: 14px;
            font-family: $default-heading-font;
            font-weight: 500;
            color: $default-heading-color;
            display: inline-block;
            margin-right: 5px;

            &:hover {
                color: $first-main-color;
            }

            &:after{
                content: '';
                display: block;
                border-bottom: 1px solid;
                border-color: inherit;
                position: relative;
                top: -4px;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.eltdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    textarea#comment {
        height: 133px;
        resize: vertical;
        overflow: auto;
    }

    .eltdf-comment-form-inner {
        padding: 72px 0 17px;
    }

    .comment-reply-title {
        margin: 0 0 31px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .eltdf-comment-holder {

        .eltdf-comment-list {

            .children {
                padding: 0 0 0 20px;
            }
        }
    }
}

@include phone-landscape {

    .eltdf-comment-holder {

        .eltdf-comment-list {

            .children {
                padding: 0;
            }
        }
    }
}

@include ipad-portrait {
    .eltdf-comment-holder {
        .eltdf-comment-image {
            display: none;
            width: 100%;
        }
        .eltdf-comment-text {
            display: block;
            width: 100%;
        }
    }
}

@include phone-portrait {

    .eltdf-comment-holder {

        .eltdf-comment-image {
            float: none;
        }

        .eltdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.eltdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }

    &.wpcf7-textarea{
        height: 160px;
    }
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
    @include eltdfButtonDefaultStyle();
    @include eltdfButtonSize();
    @include eltdfButtonSolidColor();
    line-height: initial;
    background-color: $first-main-color;
    cursor: pointer;

    &:hover {
        @include eltdfButtonSolidHoverColor();
        background-color: $default-heading-color;
    }

}

p.form-submit,
.wpcf7-form p {
    margin: 0;
}

#respond textarea,
.eltdf-style-form textarea,
.wpcf7-form textarea {
    display: block;
}

.wpcf7-form textarea {
    display: inline-block;
}

.eltdf-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.eltdf-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.eltdf-slider-is-moving {
        .owl-item {
            .eltdf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.eltdf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include eltdfTransform(translateY($pos));
            }
        }
    }

    &.eltdf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 0;
            margin: 0;
            color: $default-heading-color;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;

            @include eltdfTransform(translateY(-50%));
            @include eltdfTransition(color .2s ease-out);

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 41px;
                line-height: 1;
                @include eltdfTransition(.2s);
                backface-visibility: hidden;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: 30px;

            &:hover {
                span {
                    @include eltdfTransform(translateX(-4px));
                }
            }
        }

        .owl-next {
            right: 30px;

            &:hover {
                span {
                    @include eltdfTransform(translateX(4px));
                }
            }
        }

        @include ipad-landscape {
            display: none;
        }
    }

    .owl-dots {
        text-align: center;
        margin: 16px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 6px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 12px;
                height: 12px;
                border-radius: 3em;
                background-color: transparent;
                border: 2px solid rgba($default-heading-color, .5);
                box-sizing: border-box;

                @include eltdfTransition(background-color .2s ease-out, border .2s ease-out);
            }

            &:hover,
            &.active {

                span {
                    background-color: $default-heading-color;
                    border-color: $default-heading-color;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .eltdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .eltdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                @include eltdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.eltdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .eltdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
    .eltdf-wrapper {
        padding: 0 !important;
    }

    .eltdf-content {
        height: calc(100vh - 95px);

    }

    &.admin-bar {
        .eltdf-content {
            height: calc(100vh - 127px);
        }
    }

    .eltdf-content-inner {
        @include eltdfTableLayout();
    }

    .eltdf-page-not-found {
        @include eltdfTableCellLayout();

        padding: 0 30%;
        text-align: center;

        .eltdf-404-wrapper{
            background-color: #fff;
            padding: 90px 19% 105px 19%;
            position: relative;

            .eltdf-separator{
                width: 55px;
                height: 4px;
                border-bottom: 4px solid $first-main-color;
                margin: 22px 0;
            }
        }

        .eltdf-404-title {
            margin: 0;
        }

        .eltdf-404-subtitle {
            margin: 35px 0 0;
            font-family: Quentin,serif;
            color: #ff1949;
            font-size: 150px;
            font-weight: 400;
            position: absolute;
            bottom: 6px;
            right: -165px;
        }

        .eltdf-404-text {
            margin: 10px 0 0;
            font-size: 19px;
            font-weight: 300;
            font-family: $default-heading-font;
            color: $default-text-color;
            line-height: 32px;
        }

        .eltdf-btn.eltdf-btn-solid:not(.eltdf-btn-custom-border-hover) {
            margin: 40px 0 0;
            background-color: $first-main-color;

            &.eltdf-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include laptop-landscape-mac{
        .eltdf-page-not-found {
            padding: 0 27%;

            .eltdf-404-wrapper {
                padding: 60px 12% 74px;
            }
        }
    }

    @include ipad-landscape {
        .eltdf-page-not-found {

            .eltdf-404-wrapper {
                padding: 90px 3% 105px
            }
        }

        .eltdf-content {
            min-height: calc(100vh - 70px);
        }

        &.admin-bar {
            .eltdf-content {
                min-height: calc(100vh - 102px);
            }
        }
    }

    @include ipad-portrait {
        .eltdf-page-not-found {

            .eltdf-404-wrapper {
                padding: 90px 16% 105px;
            }
        }

        .eltdf-page-not-found {
            padding: 70px 30px;
        }
    }

    @include phone-landscape {

        .eltdf-content {
            height: 100%;
        }

        .eltdf-page-not-found {

            .eltdf-404-wrapper {
                background-color: #fff;
                padding: 50px 10%;
                position: relative;
            }

            .eltdf-404-subtitle{
                bottom: -65px;
            }
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */

.eltdf-header-vertical #eltdf-back-to-top {
    right: 15px;
    bottom: 15px;
}

#eltdf-back-to-top {
    position: fixed;
    width: 41px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;

        @include eltdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;

        @include eltdfTransition(opacity 0.3s ease);
    }

    > span {
        position: relative;
        display: block;
        width: 39px;
        height: 39px;
        line-height: 39px;
        font-size: 15px;
        text-align: center;
        color: #fff;
        background-color: $first-main-color;
        border: 1px solid $first-main-color;
        overflow: hidden;

        @include eltdfTransition(color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out);

        i {
            @include eltdfTransition(.15s ease .05s);
        }

        &:after {
            content: '';
            position: absolute;
            z-index: 1;
            top: 14px;
            left: 18.5px;
            height: 0;
            width: 0;
            opacity: 0;
            border: 1px solid #fff;

            @include eltdfTransition(0.2s cubic-bezier(0.54, 0.13, 0, 1.43));
        }

        &:hover {

            &:after {
                height: 9px;
                opacity: 1;

                @include eltdfTransition(0.45s cubic-bezier(0.54, 0.13, 0, 1) .05s);
            }

            i {
                @include eltdfTransform(translateY(-5px));
                @include eltdfTransition(.2s cubic-bezier(1, -1.21, 0.25, 1));
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.eltdf-self-hosted-video-holder {
    .eltdf-video-wrap {
        .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

/* ==========================================================================
   #Search tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

// IE
input[type="search"]::-ms-clear {
    display: none;
}

input[type=text]::-ms-clear {
    display: none;
}

