   #Search tweak end
   ========================================================================== */

div.wpcf7-response-output{
    margin: 0 0 35px 0;
}

div.wpcf7-validation-errors, div.wpcf7-acceptance-missing {
    border: 1px solid #e1e1e1;
}

span.wpcf7-not-valid-tip{
    font-size: 12px;
}

.eltdf-contact-form-7-widget {
    .eltdf-widget-title-holder {
        margin-top: 28px;

        .eltdf-widget-title{
            position: relative;
            font-size: 29px;

            .eltdf-cf7-widget-subtitle {
                font-family: 'Quentin', serif;
                color: $first-main-color;
                font-size: 64px;
                font-weight: 400;
                position: absolute;
                top: 32px;
                right: -8px;
            }
        }
    }

    .wpcf7-form-control {
        &.wpcf7-text,
        &.wpcf7-number,
        &.wpcf7-date,
        &.wpcf7-textarea,
        &.wpcf7-select,
        &.wpcf7-quiz {
            @extend %input-style;
            border: 0;
            border-bottom: 1px solid $default-border-color;
            margin: 7px 0 20px;
            padding: 7px 10px;
            font-size: 15px;
        }

        &.wpcf7-submit{
            margin: 35px 0;
            padding: 14px 31px;
        }
    }
}

.eltdf-sidebar{
    .eltdf-contact-form-7-widget{
        background-image: url('../img/cf7-sidebar.png');
        background-repeat: no-repeat;
        background-size: contain;
        background-position-y: 100%;

        .wpcf7-submit{
            background-color: $default-heading-color;

            &:hover{
                background-color: $first-main-color;
            }
        }
    }
}

.eltdf-sidebar-holder aside.eltdf-sidebar{
    .widget_search, .widget_product_search, .widget_media_image{
        padding: 0;
        background-color: unset;
        width: 100%;
    }

    .widget_media_image{
        img{
            display: block;
        }
    }
}

// select 2 override

body {

    .select2-container--default {

        &.select2 {

            .select2-selection--single {
                background-color: transparent;
                font-style: normal;
                text-transform: initial;

                .select2-selection__arrow {
                    right: 21px;

                    &:before {
                        position: relative;
                        top: 2px;
                        left: 4px;
                        content: "\54";
                        font-family: "dripicons-v2" !important;

                    }
                }

                .select2-selection__rendered {
                    padding: 0 44px 0 22px;
                    text-align: left;
                }

            }
        }

        &.select2-container {
            .select2-dropdown {
                .select2-results__options {
                    padding: 2px 22px;
                }

                .select2-results__option {
                    font-style: normal;
                }
            }
        }

    }
}

.boxed-footer-custom-menu {
    text-align: center;

    .boxed-footer-custom-menu-inner {
        ul {
            li {
                display: inline-block;
                padding: 5px 16px;
                margin: 0;

                @include phone-portrait {
                    display: block;
                }

                a {
                    position: relative;
                    font-family: $default-heading-font;
                    font-weight: 600;
                    font-size: 17px;
                    color: #fff;

                    &:before {
                        content: '';
                        width: 0;
                        border-bottom: solid 1px #fff;
                        position: absolute;
                        left: 0;
                        top: 100%;
                        z-index: 1;
                        @include eltdfTransition(.3s);
                    }
    
                    &:hover {
                        &:before {
                            width: 100%;
                        }
                    }
                }
            }
        }
    }
}