.eltdf-top-bar {
	.widget {
		color: #b6b6b6;
		letter-spacing: 0.015em;
		font-family: $default-heading-font;

		a {
			color: #b6b6b6;

			&:hover {
				color: $first-main-color;
			}
		}

		&.eltdf-social-icons-group-widget .eltdf-social-icon-widget-holder {
			font-size: 12px;
			margin: 0 7px 0 0;
			line-height: 46px;
		}
	}
}