footer {

    a, body, p a{
        color: #fff;
    }


    color: #e1e1e1;

    $default-heading-color: #fff;
    $widget-background-color: transparent;

    // widgets spacing
    $widget-bottom-margin: 45px;
    $widget-bottom-padding: 15px;
    $widget-inner-padding: 15px;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 15px;

    // list items color
    $widget-list-item-background-color: #999999;

    // list items border
    $widget-list-item-border-color: #cccccc;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: solid;

    // text
    $widget-text-color: #ffffff;
    $widget-heading-color: #999999;

    // links
    $widget-link-color: #eeeeee;
    $widget-link-hover-color: $first-main-color;

    // specific widgets
    $widget-search-form-height: $input-height;
    $widget-tag-cloud-height: 30px;

    @import 'widgets/default-wordpress-widgets-boxed';
    @import 'widgets/standalone-widgets';

    .widget{
        padding: 0;
        margin: 0 0 35px;

        .eltdf-cli-text{
            .eltdf-cli-title a:hover{
                color: $first-main-color !important;
                border-bottom: 0 !important;
            }

            >a:hover{
                span{
                    color: $first-main-color !important;
                }
            }
        }

        ul {
            li{
                padding: 7px 0;
                font-size: 15px;
            }
        }

        option{
            color: #444 !important;
        }
        .tagcloud {
            a {
                color: #444444;
            }
        }

        .eltdf-searchform .input-holder button {
            background-color: transparent;

            span {
                color: #fff;
            }
        }
    }

}